const MD5 = require("md5");

const passwordKey = "c2Rm@#Zz@MR|||sdfg453|||5reg6665ZnSas@#sdfgsDSYXY";

export default class Crypt {
  // eslint-disable-next-line class-methods-use-this
  CryptPassword(pass) {
    return MD5(MD5(MD5(pass) + passwordKey));
  }

  // eslint-disable-next-line class-methods-use-this
  decryptUserInfo(hash) {
    const splitHash = hash.split(".");
    if (!splitHash[1]) return false;

    return JSON.parse(atob(splitHash[1]));
  }
}
